import React from 'react';
import moment from 'moment';
import 'moment/locale/id';

moment.locale('id');

export default props => (
  <svg className="card-img-top" width="100%" height="130" textAnchor="middle" alignmentBaseline="middle">
    <rect fill="#e74c3c" width="100%" height="100%"></rect>
    <text x="50%" y="60%" fontSize="50pt" fill="white">{moment(props.date).format('DD')}</text>
    <text x="50%" y="80%" fontSize="20pt" fill="white">{moment(props.date).format('MMMM')}</text>
  </svg>
)