import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom';
import MainDash from '../components/MainDash';
import Users from '../components/Users';
import Purpose from './Purpose';
import Settings from './Settings';
import Queue from './Queue';
import QueueRecap from './QueueRecap';
import {CardBody} from 'reactstrap';
import {IoIosLink} from 'react-icons/io';

export default class DashContent extends Component {

  render() {
    return (
      <div className="col-md-9 overflow-scroll">
        <Switch>
          <Route path={`${this.props.match.path}main`} component={MainDash} />
          <Route exact path={`${this.props.match.path}user`} render={() => (<Users models={this.props.models} />)} />
          <Route exact path={`${this.props.match.path}pengaturan`} render={() => (<Settings models={this.props.models} />)} />
          <Route exact path={`${this.props.match.path}tujuan`} render={() => (<Purpose models={this.props.models} />)} />    
          {this.props.user.type === 'operator' && <Route exact path={`${this.props.match.path}antrian`} render={() => (<Queue socket={this.props.socket} provider={this.props.provider} models={this.props.models} />)} />}    
          <Route exact path={`${this.props.match.path}rekap-antrian`} render={() => (<QueueRecap socket={this.props.socket} provider={this.props.provider} models={this.props.models} />)} /> 
          <Route component={NotFound} />
        </Switch>
        {/* <div className="card-body h-100">
        </div> */}
      </div>
    )
  }
}

const NotFound = () => (
  <CardBody className="d-flex flex-column justify-content-center align-items-center h-100">
    <IoIosLink size="15em" color="#aaa" />
    <h3 className="m-0" style={{ color : '#aaa' }}>Halaman tidak ada</h3>
  </CardBody>
)