import React, { Component } from 'react';
import { Button, Modal, ModalBody, ModalHeader, ButtonGroup, Col, Spinner, Alert } from 'reactstrap';
import moment from 'moment';
import AddSetting from './AddSetting';
import SettingCard from './SettingCard';
import AddDate from './AddDate';
import { IoMdAdd } from 'react-icons/io';

export default class Settings extends Component {
  state = {
    schedules: {
      rows: [],
      count: 1
    },
    modal: false,
    schedule: {
      date: new Date()
    },
    addSchedule: false,
    totalPage: 1,
    page: 1,
    offset: 0,
    limit: 4,
    loading: false
  }

  getSchedule = (refresh = false) => {
    const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit } = this.state;
    if (refresh) this.setState({ loading: true })
    Schedule.collection({ limit, offset, order: [['date', 'asc']] }).then(schedules => {
      totalPage = Math.ceil(schedules.count / limit);
      this.setState({ schedules, totalPage, loading : false })
    });
  }

  componentDidMount = () => {
    this.getSchedule(true);
  }

  delete = schedule => {
    // this.setState({ modal : false })
    // console.log(schedule.delete);
    schedule.delete().then(() => {
      this.setState({ schedule: {}, modal: false });
      this.getSchedule();
    })
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getSchedule());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getSchedule());
  }

  first = () => {
    this.setState({ offset: 0, page: 1 }, () => this.getSchedule());
  }

  last = () => {
    const { totalPage, limit } = this.state;
    const offset = parseInt((totalPage - 1) * limit);
    this.setState({ offset, page: totalPage }, () => this.getSchedule());
  }


  render() {
    document.title = "Booking | Pengaturan"
    const { schedules: { rows } } = this.state;
    return (
      <div className="card-body">
        <h4 className="m-0">Pengaturan</h4>
        <hr />
        <Button outline onClick={() => this.addSetting.open()} color="success"><IoMdAdd />&nbsp;Tambah Pengaturan</Button>
        {/* <Button outline color="success" className="rounded-pill" onClick={() => this.setState({ addSchedule: true })} ><IoMdAdd /></Button> */}
        <hr />
        <div className="row">
          {this.state.loading && <Col md="12">
            <Alert color="dark" className="text-center"><h4 className="m-0"><Spinner />&nbsp;Loading</h4></Alert>
          </Col>}
          {rows.map((row, i) => (<SettingCard deleteSetting={schedule => this.setState({ schedule, modal: true })} key={i} setting={row} />))}
          {this.state.addSchedule && <AddDate lastDate={rows[(rows.length - 1)].date} />}
        </div>
        <div className="d-flex flex-row justify-content-between align-items-center my-3">
          {this.state.totalPage > 0 &&
            <ButtonGroup size="sm">
              <Button disabled={this.state.loading} outline onClick={this.first} color="primary">First</Button>
              <Button disabled={this.state.loading} outline onClick={this.prev} color="dark">Prev</Button>
              <Button color="light" disabled>Halaman {this.state.page}/{this.state.totalPage}</Button>
              <Button disabled={this.state.loading} outline onClick={this.next} color="dark">Next</Button>
              <Button disabled={this.state.loading} outline onClick={this.last} color="primary">Last</Button>
            </ButtonGroup>
          }
        </div>
        <AddSetting models={this.props.models} get={this.getSchedule} ref={ref => this.addSetting = ref} models={this.props.models} />
        <DeleteSetting schedule={this.state.schedule} delete={schedule => this.delete(schedule)} isOpen={this.state.modal} close={() => this.setState({ modal: false })} />
      </div>
    )
  }
}

const DeleteSetting = props => (
  <Modal isOpen={props.isOpen}>
    <ModalHeader className="bg-danger" toggle={props.close} />
    <ModalBody>
      <h5 className="text-center">Apakah Anda yakin ingin menghapus penganturan ini?</h5>
      <div className="row my-3">
        <div className="col-md-6">
          <Button color="danger" onClick={() => props.delete(props.schedule)} block>Ya</Button>
        </div>
        <div className="col-md-6">
          <Button color="light" onClick={props.close} block>Tidak</Button>
        </div>
      </div>
    </ModalBody>
  </Modal>
)