import React, { Component } from 'react';
import { Modal, ModalBody, ModalHeader, Alert, Spinner} from 'reactstrap';
import Input from './Input';

export default class RegisterUser extends Component {
  state = {
    name: "",
    username: "",
    password: "",
    type: "administrator",
    edit: false,
    modal: false,
    loading: false
  }

  onChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value });
  }

  submit = ev => {
    ev.preventDefault();
    this.setState({ loading: true });
    const { username, name, password, type } = this.state;
    this.props.onClick({ username, name, password, type }, () => {
      this.setState({ loading: false }, this.clear())
    })
  }

  clear = () => this.setState({
    name: "",
    username: "",
    password: "",
    type: "administrator",
    edit: false,
    modal: false
  })

  editSubmit = ev => {
    ev.preventDefault();
    const { username, name, type, password } = this.state;
    this.props.update({ username, name, type, password }, this.clear);
  }

  componentWillReceiveProps = props => {
    const { user: { name, type, username }, edit } = props;
    this.setState({ name, type, username, edit });
  }

  open = () => this.setState({ modal: true });

  close = () => this.setState({ modal: false });


  render() {
    return (
      <Modal size="lg" toggle={this.close} isOpen={this.state.modal}>
        <ModalHeader color="success" toggle={this.close}>{this.state.edit ? `Edit ${this.state.name}` : `Tambah Pengguna`}</ModalHeader>
        <ModalBody>
          <form className="form-group">
            <Input placeholder="Nama Lengkap" type="text" name="name" value={this.state.name} onChange={this.onChange} />
            <Input placeholder="Username" type="text" name="username" value={this.state.username} onChange={this.onChange} />
            <Input placeholder="Password" type="password" name="password" value={this.state.password} onChange={this.onChange} />
            <select name="type" value={this.state.type} onChange={ev => this.setState({ type: ev.target.value })} className="form-control form-control-lg rounded-pill">
              <option value="">Pilih tipe user</option>
              <option value="administrator">Administrator</option>
              <option value="operator">Operator</option>
            </select>
            <hr />
            { this.state.loading &&
              <>
                <hr/>
                <Alert color="success"><Spinner size="sm" />&nbsp;Loading</Alert>
                <hr/>
              </>
            }
            {!this.state.edit && <button type="button" onClick={this.submit} className="btn btn-success">Daftar</button>}
            {this.state.edit && <button type="button" onClick={this.editSubmit} className="btn btn-success">Simpan</button>}
            {this.state.edit && <button type="button" onClick={this.clear} className="btn btn-danger">Batal</button>}
          </form>
        </ModalBody>
      </Modal>
    )
  }
}
