import React, { Component } from 'react'
import { Modal, ModalBody, ModalHeader, ModalFooter, Button, Table, InputGroup, InputGroupAddon, Input, Spinner } from 'reactstrap';
// import Input from './Input';

export default class Requirement extends Component {
  state = {
    open: false,
    name: "",
    requirements: {
      rows: [],
      count: 1
    },
    page: 1,
    offset: 0,
    limit: 10,
    totalPage: 1,
    submitting : false,
    requirement : {
      name : ''
    },
    edit : false,
    loading : false
  }

  componentDidMount = () => {
    this.getList(true);
  };
  

  getList = (loading = false) => {
    const { models: { Requirement } } = this.props;
    if (loading) this.setState({ loading : true });
    Requirement.collection({ limit: 5, offset: 0 }).then(requirements => {
      const { limit } = this.state;
      const totalPage = Math.ceil(requirements.count / limit);
      this.setState({ requirements, totalPage, loading : false });
    });
  }

  cancel = () => {
    this.setState({ requirement : {name : ''}, edit : false, name : '' });
  }

  close = () => {
    this.setState({ open: false });
  }

  edit = requirement => this.setState({ edit : true, requirement, name : requirement.name });

  delete = requirement => {
    requirement.delete().then(() => this.setState({ requirement : { name : ''}, edit : false }, () => this.getList()))
  }

  update = () => {
    const { requirement, name } = this.state;
    requirement.update({ name }).then(() => this.setState({ name : '', requirement : { name : ''}, edit : false }, () => this.getList()));
  }

  addRequirement = ev => {
    ev.preventDefault();
    const { models: { Requirement } } = this.props;
    const { name } = this.state;
    this.setState({ submitting : true }, () => {
      Requirement.create({ name }).then(() => {
        this.setState({ name: '', submitting : false }, () => this.getList());
      })
    })
  }

  render() {
    return (
      <>
        <Modal size="lg" isOpen={this.state.open} backdrop="static">
          <ModalHeader className="bg-danger border-bottom border-danger text-white" toggle={this.close}>Tambah Dokumen</ModalHeader>
          <ModalBody>
            <form action="" method="post">
              <InputGroup>
                <Input readOnly={this.state.submitting} placeholder="Nama Dokumen" onChange={ev => this.setState({ name: ev.target.value })} value={this.state.submitting ? 'Menambah Dokumen' : this.state.name} name="name" />
                <InputGroupAddon addonType="append">
                  { this.state.edit && <Button color="danger" outline onClick={this.cancel}>Cancel</Button>}
                  { this.state.edit && <Button color="success" outline onClick={this.update}>Simpan</Button>}
                  {!this.state.edit && <Button color="success" outline onClick={this.addRequirement} >Tambah</Button>}
                </InputGroupAddon>
              </InputGroup>
              <hr/>
            </form>
          </ModalBody>
          <Table striped hover bordered>
            <thead>
              <tr>
                <th>No.</th>
                <th>Nama</th>
                <th>Edit/Hapus</th>
              </tr>
            </thead>
            <tbody>
              { this.state.loading ?
                <tr>
                  <td colSpan={3}><p className="text-muted m-0 text-center"><Spinner size="sm" color="secondary" /> Loading</p></td>
                </tr>
                :
                this.state.requirements.rows.length > 0 ?
                this.state.requirements.rows.map((req, i) => (<Tr key={i} edit={this.edit} deleteRequirement={this.delete} req={req} index={i + 1} />)) :
                <tr>
                  <td colSpan={3}><p className="text-muted m-0 text-center">Belum Ada Dokumen Persyaratan</p></td>
                </tr>
              }
            </tbody>
          </Table>
          <ModalFooter>
            <Button outline onClick={this.close} color="dark" size="sm">Tutup</Button>
          </ModalFooter>
        </Modal>
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.index}</td>
    <td>{props.req.name}</td>
    <td>
      <div className="btn-group btn-group-sm">
        <button onClick={() => props.edit(props.req)} className="btn btn-warning">Edit</button>
        <button onClick={() => props.deleteRequirement(props.req)} className="btn btn-danger">Hapus</button>
      </div>
    </td>
  </tr>
)