import React, { Component } from 'react';
import { Modal, ModalBody, ModalHeader, Table, Button, ModalFooter } from 'reactstrap';
import Select from 'react-select';

export default class EditPurpose extends Component {
  state = {
    open: false,
    reqs: [],
    req: '',
    name: "",
    loading : false,
    requirements : []
  }

  getRequirements = () => {
    this.setState({ loading : true });
    const { models: { Requirement } } = this.props;
    Requirement.collection({ limit: 999, offset: 0 }).then(requirements => {
      const reqs = requirements.rows.map(req => ({ value: req.name, label: req.name, disabled : false }));
      this.setState({ reqs }, () => this.check());
    })
  }

  check = () => {
    const { reqs, requirements } = this.state;
    for (let i = 0; i < reqs.length; i++) {
      for (let j = 0; j < requirements.length; j++) {
        if (reqs[i].label === requirements[j].name) reqs[i].disabled = true;
      }
    }
    this.setState({ reqs, loading : false });
  }

  open = () => {
    const { requirements: { name, requirements } } = this.props;
    this.setState({ open: true, name, requirements }, () => this.getRequirements())
  };

  close = () => this.setState({ open: false });

  save = () => {
    const { name, requirements : req } = this.state;
    const { requirements } = this.props;
    requirements.update({ name, requirements : req }).then(() => {
      this.close();
      this.props.reload();
    })
  }

  addReq = () => {
    const { req, reqs, requirements } = this.state;
    // const { requirements : {requirements} } = this.props;
    for(let i = 0; i < reqs.length; i++) 
    {
      if (reqs[i].label === req.label) reqs[i].disabled = true;
    }
    
    requirements.push({ name : req.label, required : true });
    this.setState({ reqs, req : '', requirements });
  }
  
  removeReq = req => {
    const {requirements, reqs} = this.state;
    for(let i = 0; i < reqs.length; i++) 
    {
      if (reqs[i].label === req.name) reqs[i].disabled = false;
    }
    const r = requirements.filter(r => r.name !== req.name);
    this.setState({ requirements : r });
  }
  
  checkClicked = req => {
    let { requirements } = this.state;
    requirements[req.index].required = !requirements[req.index].required;
    this.setState({ requirements })
  }

  render() {
    const { requirements } = this.state;
    return (
      <>
        <Modal backdrop="static" isOpen={this.state.open} size="lg" style={{ maxWitdh: '80%' }}>
          <ModalHeader className="bg-warning border-warning" toggle={this.close} ></ModalHeader>
          <ModalBody>
            <input type="text" placeholder="Tujuan" onChange={ev => this.setState({ name : ev.target.value })} className="form-control rounded-pill my-3" value={this.state.name} />
            <hr/>
            {!this.state.loading && <Select 
            value={this.state.req} 
            onChange={req => this.setState({ req })}
            isOptionDisabled={opt => opt.disabled}
            placeholder="Pilih Dokumen"
            options={this.state.reqs} />}
            { this.state.req === '' ?
            <Button color="success" size="sm" disabled className="my-2" >Tambah</Button>
            :
            <Button color="success" size="sm" onClick={this.addReq} className="my-2">Tambah</Button>
            }
          </ModalBody>
          <Table bordered hover striped>
            <thead>
              <tr>
                <th>Nama</th>
                <th>Opsional</th>
                <th>Hapus</th>
              </tr>
            </thead>
            <tbody>
              {requirements.length > 0 ?
                requirements.map((req, i) => (<Tr checkClicked={this.checkClicked} removeReq={this.removeReq} {...req} key={i} index={i} />)) :
                <tr>
                  <td colSpan={3}><p className="m-0 text-muted text-center">Tidak ada persyaratan</p></td>
                </tr>
              }
            </tbody>
          </Table>
          <ModalFooter>
            <Button color="success" onClick={this.save} outline>Simpan</Button>
          </ModalFooter>
        </Modal>
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.name}</td>
    <td><input type="checkbox" onChange={() => props.checkClicked(props)} value={!props.required} checked={props.required} /></td>
    <td><Button onClick={() => props.removeReq(props)} color="danger" size="sm" outline>Hapus</Button></td>
  </tr>
)