import React, { Component } from 'react';
import { Modal, ModalBody, ModalHeader, ModalFooter, Button, Alert, Spinner } from 'reactstrap';
import Input from './Input';
import Select from 'react-select';
import { FaTrash } from 'react-icons/fa';

export default class AddPurpose extends Component {
  state = {
    open: false,
    name: "",
    requirements: [],
    fields: [],
    requirement: '',
    required: [],
    loading: false,
    filter: [],
  }

  open = () => {
    this.setState({ open: true });
    this.getRequirements()
  }

  close = () => {
    this.setState({ open: false });
  }

  add = ev => {
    ev.preventDefault();
    const { name, fields } = this.state;
    this.props.add({ name: name, requirements: fields });
  }

  removeRequirement = name => {
    const { requirements, fields } = this.state;
    const fds = fields.filter(field => field.name !== name);
    for (let i = 0; i < requirements.length; i++) {
      if (name === requirements[i].label) requirements[i].disable = false;
    }
    this.setState({ requirements, fields : fds });
  }

  addRequirement = () => {
    const { requirement, fields, filter, requirements } = this.state;
    // const reqs = requirements.filter(req => req.value !== requirement.value);
    fields.push({ name: requirement.value, required: true, });
    for (let i = 0; i < requirements.length; i++) {
      if (requirement.label === requirements[i].label) requirements[i].disable = true;
    }
    this.setState({ fields, requirement: '', filter, requirements });
  }

  getRequirements = () => {
    const { models: { Requirement } } = this.props;
    this.setState({ loading : true })
    Requirement.collection({ limit: 999, offset: 0 }).then(reqs => {
      const requirements = reqs.rows.map(req => ({ value: req.name, label: req.name, disable : false }));
      this.setState({ requirements, loading : false });
    })
  }

  onChange = ev => {
    const { fields } = this.state;
    fields[ev].required = !fields[ev].required;
    this.setState({ fields });
  }

  render() {
    return (
      <Modal isOpen={this.state.open} size="lg" backdrop="static">
        <ModalHeader>Tambah Tujuan</ModalHeader>
        <ModalBody>
          <Input name="name" placeholder="Tujuan" onChange={ev => this.setState({ name: ev.target.value })} />
          {this.state.requirements.length > 0 ?
            <>
              {!this.state.loading && <Select
                value={this.state.requirement}
                onChange={requirement => this.setState({ requirement })}
                placeholder="Pilih Dokumen"
                ref="select"
                isOptionDisabled={opt => opt.disable}
                options={this.state.requirements} />}
              <hr />
              <button disabled={this.state.requirement === ''} type="submit" onClick={this.addRequirement} className="btn btn-success btn-sm rounded-pill">Tambah Persyaratan</button>
            </> :
            this.state.loading ?
            <Alert color="info"><Spinner color="info" size="sm" />&nbsp;Loading</Alert>
            :
            <Button block outline color="success" onClick={() => {
              this.close();
              this.props.addRequirement();
            }} size="lg" >Tambah Dokumen Persyaratan</Button>
          }
        </ModalBody>
        <table className="table table-striped table-bordered">
          <thead>
            <tr>
              <th>Persyaratan</th>
              <th>Opsional</th>
              <th>Hapus</th>
            </tr>
          </thead>
          <tbody>
            {this.state.fields.length === 0 && <tr><td colSpan="3"><p className="m-0 text-center text-muted">Tambah dokumen jika diperlukan</p></td></tr>}
            {this.state.fields.map((field, i) => (<Tr removeRequirement={this.removeRequirement} checked={field.required} onChange={this.onChange} index={i} key={i} {...field} />))}
          </tbody>
        </table>
        <ModalFooter>
          <Button color="success" outline onClick={this.add} className="rounded-pill" >Tambah Tujuan</Button>
          <Button color="danger" outline onClick={this.close} className="rounded-pill" >Tutup</Button>
        </ModalFooter>
      </Modal>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.name}</td>
    <td><input type="checkbox" onChange={() => props.onChange(props.index)} value={!props.required} checked={props.required} /></td>
    <td><button onClick={() => props.removeRequirement(props.name)} className="btn btn-outline-danger btn-sm"><FaTrash /></button></td>
  </tr>
)