import React, { Component } from 'react'
import { Card, CardBody, Button, Tooltip, ButtonGroup, Table, Row, Col, Input, InputGroup, InputGroupAddon, InputGroupText, Spinner } from 'reactstrap'
import { FaFileExcel, FaFilePdf } from 'react-icons/fa';
import { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io';
import Flatpickr from 'react-flatpickr';
import moment from 'moment';
import 'flatpickr/dist/themes/airbnb.css';
import 'moment/locale/id';
import PreviewDocuments from './PreviewDocuments';

export default class QueueRecap extends Component {
  state = {
    tooltips: [
      { open: false },
      { open: false },
    ],
    msg: '',
    limit: 10,
    offset: 0,
    page: 1,
    totalPage: 1,
    queues: {
      rows: [],
      count: 1
    },
    loading: false,
    date: [new Date()],
    documents: []
  }

  componentDidMount = () => {
    document.title = "Laporan"
    moment.locale('id');
    this.getQueues(true);
  }

  tooltipToggle = () => {
    this.setState({ tooltips: [{ open: false }, { open: false }] })
  }

  onHover = ev => {
    const { tooltips } = this.state;
    // console.log(ev.target.name)
    if (typeof ev.target.name !== 'undefined') tooltips[parseInt(ev.target.name)].open = true;
    this.setState({ tooltips });
  }

  getQueues = (loading = false) => {
    if (loading) this.setState({ loading: true })
    const { models: { Queue } } = this.props;
    const { limit, offset } = this.state;
    Queue.collection({
      limit, offset, include: [{ model: 'Purpose', attributes: ['name', 'id'] },
      { model: 'Document', attributes: ['name', 'id', 'data'] }],
      order: [['time', 'asc']],

    }).then(queues => {
      const totalPage = Math.ceil(queues.count / limit);
      this.setState({ loading: false, queues, totalPage });
    });
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getQueues());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getQueues());
  }

  onLimitChange = ev => this.setState({ limit: ev.target.value }, () => this.getQueues());

  onDateChange = date => this.setState({ date }, () => console.log(this.state.date));

  previewDocs = documents => this.setState({ documents }, () => this.preview.open());

  render() {
    const opt = {
      mode: "range"
    }
    const { documents } = this.state
    return (
      <>
        <CardBody className="border-bottom border-primary">
          <h5 className="m-0">Laporan</h5>
          <hr />
          <Row className="justify-content-between" noGutters>
            <Col md="2">
              <ButtonGroup size="sm">
                <Button onClick={this.prev} color="primary"><IoIosArrowBack /></Button>
                <Button disabled color="dark">{`${this.state.page}/${this.state.totalPage}`}</Button>
                <Button onClick={this.next} color="primary"><IoIosArrowForward /></Button>
              </ButtonGroup>
            </Col>
            <Col md="2">
              <ButtonGroup size="sm">
                <Button color="light" disabled>Export File</Button>
                <Button onMouseOver={this.onHover} name="0" id="btn0" color="success" title="Export ke excel"><FaFileExcel onMouseOver={this.onHover} /></Button>
                {this.state.tooltips[0].open && <Tooltip placement="top" autohide={false} toggle={this.tooltipToggle} isOpen={this.state.tooltips[0]} target="btn0">Export ke Excel</Tooltip>}
                <Button onMouseOver={this.onHover} name="1" id="btn1" color="danger" title="Export ke excel"><FaFilePdf onMouseOver={this.onHover} /></Button>
                {this.state.tooltips[1].open && <Tooltip placement="top" autohide={false} toggle={this.tooltipToggle} isOpen={this.state.tooltips[1]} target="btn1">Export ke PDF</Tooltip>}
              </ButtonGroup>
            </Col>
            <Col md="3">
              <InputGroup size="sm">
                <Flatpickr value={this.state.date} options={opt} onChange={this.onDateChange} placeholder="Tanggal" className="form-control form-control-sm" />
                <InputGroupAddon addonType="append"><InputGroupText className="text-white bg-dark border-dark">Tanggal</InputGroupText></InputGroupAddon>
              </InputGroup>
            </Col>
            <Col md="2">
              <InputGroup size="sm">
                <Input value={this.state.limit} onChange={this.onLimitChange} type="number" />
                <InputGroupAddon addonType="append">Data/Halaman</InputGroupAddon>
              </InputGroup>
            </Col>
          </Row>
        </CardBody>
        <Table bordered hover striped>
          <thead>
            <tr>
              <th>No. Antrian</th>
              <th>Jam</th>
              <th>Tanggal</th>
              <th>Nama</th>
              <th>Tujuan</th>
              <th>NIK</th>
              <th>Status</th>
              <th>Preview</th>
            </tr>
          </thead>
          <tbody>
            {this.state.queues.rows.length === 0 && !this.state.loading &&
              <tr><td className="text-center text-muted" colSpan={document.getElementsByTagName('th').length}>Belum ada antrian</td></tr>
            }
            {this.state.loading && <tr>
              <td className="text-center text-muted" colSpan={document.getElementsByTagName('th').length}><Spinner size="sm" color="secondary" />&nbsp;Loading</td>
            </tr>}
            {this.state.queues.rows.map(queue => <Queue onClick={this.previewDocs} {...queue} key={queue.id} />)}
          </tbody>
        </Table>
        <CardBody className="border-top border-primary">
          <Row className="justify-content-between" noGutters>
            <Col md="2">
              <ButtonGroup size="sm">
                <Button onClick={this.prev} color="primary"><IoIosArrowBack /></Button>
                <Button disabled color="dark">{`${this.state.page}/${this.state.totalPage}`}</Button>
                <Button onClick={this.next} color="primary"><IoIosArrowForward /></Button>
              </ButtonGroup>
            </Col>
            <Col md="2">
              <InputGroup size="sm">
                <Input value={this.state.limit} onChange={this.onLimitChange} type="number" />
                <InputGroupAddon addonType="append">Data/Halaman</InputGroupAddon>
              </InputGroup>
            </Col>
          </Row>
        </CardBody>
        <PreviewDocuments documents={documents} ref={ref => this.preview = ref} />
      </>
    )
  }
}

const Queue = props => (
  <tr>
    <td>{props.queue_number}</td>
    <td>{props.time}</td>
    <td>{moment(props.date).format('DD MMMM YYYY')}</td>
    <td>{props.name}</td>
    <td>{props.purpose.name}</td>
    <td>{props.nik}</td>
    <td>{props.status}</td>
    <td><Button color="info" onClick={() => props.onClick(props.documents)} size="sm">File</Button></td>
  </tr>
)