import React, { Component } from 'react';
import { Card, Table, CardFooter, Button, Input } from 'reactstrap';
import moment from 'moment';
import CardDate from './CardDate';

export default class AddDate extends Component {
  state = {
    openModal: false,
    date: new Date(),
    open: "08:00",
    close: "16:00",
    break_start: "13:00",
    break_end: "14:00",
    tolerance: 1
  }

  componentDidMount = () => {
    // let { date } = this.state;
    const { lastDate } = this.props;
    const date = moment(lastDate).add(1, 'day');
    this.setState({ date });
  }

  onChange = ev => {
    this.setState({ [ev.target.name]: ev.target.value })
  }

  render() {
    return (
      <div className="col-md-3 my-2">
        <Card className="border-0 shadow-sm">
          <CardDate {...this.state} />
          <Table bordered striped>
            <tbody>
              <tr>
                <td>Jam Buka</td>
                <td><Input name="open" onChange={this.onChange} value={this.state.open} bsSize="sm" type="time" /></td>
              </tr>
              <tr><td>Jam Tutup</td><td><Input name="close" onChange={this.onChange} value={this.state.close} type="time" /></td></tr>
              {/* <tr><td>Jam Istirahat</td><td>{props.setting.break_start}</td></tr> */}
              {/* <tr><td>Jam Selesai Istirahat</td><td>{props.setting.break_end}</td></tr> */}
              {/* <tr><td>Toleransi</td><td>{props.setting.tolerance} menit</td></tr> */}
            </tbody>
          </Table>
          {/* <CardFooter>
            <Button onClick={() => props.deleteSetting(props.setting)} color="danger" outline size="sm" className="rounded-pill">Hapus</Button>
          </CardFooter> */}
        </Card>
      </div>
    )
  }
}