import React, { Component } from 'react'
import RegisterUser from './RegisterUser';
import {Spinner} from 'reactstrap';
import { IoIosAddCircle } from 'react-icons/io';

export default class Users extends Component {
  state = {
    users: {
      rows: [],
      count: 0
    },
    limit: 10,
    offset: 0,
    page: 1,
    total_page: 1,
    user: {
      name: '',
      username: '',
      type: ''
    },
    edit: false,
    loading : false
  }

  componentDidMount = () => {
    document.title = "Pengguna";
    this.getUser(true);
  }

  getUser = (loading = false) => {
    const { models: { User } } = this.props;
    const { limit, offset } = this.state;
    if (loading) this.setState({ loading : true });
    User.collection({ limit, offset }).then(users => {
      const total_page = Math.ceil(users.count / limit);
      this.setState({ users, total_page, loading : false });
    });
  }

  next = () => {
    let { limit, page, total_page } = this.state;
    page = (page < total_page) ? (page + 1) : total_page;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, total_page, page }, () => this.getUser());
  }

  prev = () => {
    let { limit, page, total_page } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, total_page, page }, () => this.getUser());
  }

  addUser = (data, cb) => {
    const { models: { User } } = this.props;
    User.create(data).then(() => {
      this.getUser()
      cb();
    });
  }

  deleteUser = user => {
    // console.log(user)
    user.delete().then(() => this.getUser());
  }

  updateUser = (data, cb) => {
    const { user } = this.state;
    user.update(data).then(() => {
      cb();
      this.getUser()
      this.setState({ user: {}, edit: false });
    })
  }

  edit = user => this.setState({ user, edit: true }, () => this.register.open());

  render() {
    return (
      <>
        <div className="card-body">
          <h5 className="mb-2">User</h5>
          <hr />
          <RegisterUser ref={ref => this.register = ref} update={this.updateUser} user={this.state.user} edit={this.state.edit} onClick={this.addUser} />
          <div className="d-flex flex-row justify-content-between">
            {this.state.total_page > 0 && <div className="btn-group btn-group-sm">
              <button className="btn btn-primary" onClick={this.prev}>Prev</button>
              <button className="btn btn-light" disabled>{this.state.page}/{this.state.total_page}</button>
              <button className="btn btn-primary" onClick={this.next}>Next</button>
            </div>}
            <button onClick={() => this.register.open()} className="btn btn-outline-success btn-sm"><IoIosAddCircle />&nbsp;Tambah Pengguna</button>
          </div>
        </div>
        <table className="table table-striped table-bordered">
          <thead>
            <tr>
              <th>No.</th>
              <th>Nama</th>
              <th>Username</th>
              <th>Tipe</th>
              <th>Edit/Delete</th>
            </tr>
          </thead>
          <tbody>
            {this.state.loading && 
              <tr>
                <td colSpan={5}><p className="text-center text-muted m-0"><Spinner size="sm" color="secondary" />&nbsp;Loading</p></td>
              </tr>
            }
            {this.state.users.rows.map((user, i) => (<Tr key={i} edit={this.edit} index={(this.state.page > 1 ? this.state.page + (i + 1) : i + 1)} onClick={this.deleteUser} user={user} />))}
          </tbody>
        </table>
        <div className="card-body">
          {this.state.total_page > 0 && <div className="btn-group btn-group-sm">
            <button className="btn btn-primary" onClick={this.prev}>Prev</button>
            <button className="btn btn-light" disabled>{this.state.page}/{this.state.total_page}</button>
            <button className="btn btn-primary" onClick={this.next}>Next</button>
          </div>}
        </div>
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.index}</td>
    <td>{props.user.name}</td>
    <td>{props.user.username}</td>
    <td>{props.user.type.charAt(0).toUpperCase() + props.user.type.slice(1)}</td>
    <td>
      <button onClick={() => props.edit(props.user)} className="btn btn-warning btn-sm">Edit</button>
      <button onClick={() => props.onClick(props.user)} className="btn btn-danger btn-sm">Hapus</button>
    </td>
  </tr>
)