import React, { Component } from 'react'
// import Input from './Input';
import AddPurpose from './AddPurpose';
import Requirement from './Requirement';
import { Modal, ModalFooter, Input,ModalHeader, Table, Button, Card, CardBody, InputGroup, InputGroupAddon, CardFooter, ButtonGroup, Row, Col, Spinner } from 'reactstrap'
import EditPurpose from './EditPurpose';
import RequirementTable from './RequirementTable';
import {FaTrash, FaRegEdit, FaBookReader} from 'react-icons/fa'

export default class Purpose extends Component {
  state = {
    purposes: {
      rows: [],
      count: 1
    },
    requirements: {
      requirements: []
    },
    modal: false,
    limit: 10,
    offset: 0,
    page: 1,
    totalPage: 1,
    loading: false
  }

  componentDidMount = () => {
    document.title = "Dokumen & Tujuan"
    this.getPurpose(true);
  }

  getPurpose = (loading = false) => {
    const { models: { Purpose } } = this.props;
    const { limit, offset } = this.state;
    if (loading) this.setState({ loading: true });
    Purpose.collection({ limit, offset }).then(purposes => {
      const totalPage = Math.ceil(purposes.count / this.state.limit)
      this.setState({ purposes, totalPage, loading: false });
    })
  }

  addPurpose = user => {
    // ev.preventDefault();
    // const { name } = this.state;
    const { models: { Purpose } } = this.props;
    Purpose.create(user).then(() => {
      this.addModal.close();
      this.getPurpose()
    });
  }

  deletePurpose = purpose => {
    purpose.delete().then(() => {
      this.getPurpose();
    })
  }

  editPurpose = requirements => this.setState({ requirements }, () => this.editModal.open());

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getPurpose());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getPurpose());
  }

  limitChange = ev => {
    this.setState({ limit : ev.target.value, page : 1, offset : 0 }, () => this.getPurpose());
  }

  render() {
    return (
      <>
        <div className="card-body">
          <h5 className="m-0">Dokumen &amp; Tujuan</h5>
          <hr />
        </div>
        <Row noGutters>
          <Col md="6" className="pr-1">
            <button className="btn btn-primary btn-sm rounded-pill mx-2" onClick={() => this.addModal.open()} >Tambah tujuan</button>
            <Card className="my-2 border-0">
              <table className="table table-striped table-bordered">
                <thead>
                  <tr>
                    <th>No.</th>
                    <th>Tujuan</th>
                    <th>Lihat Dok/Edit/Delete</th>
                  </tr>
                </thead>
                <tbody>
                  {
                    this.state.loading ?
                      <tr>
                        <td colSpan={3} className="text-center">
                          <p className="text-muted small text-center m-0"><Spinner size="sm" color="secondary" />&nbsp;Loading</p>
                        </td>
                      </tr>
                      :
                      this.state.purposes.rows.length > 0 ?
                        this.state.purposes.rows.map((purpose, i) => (<Tr key={i} edit={this.editPurpose} index={(i + 1) + this.state.offset} requirement={requirements => this.setState({ requirements, modal: true })} onClick={this.deletePurpose} purpose={purpose} />)) :
                        <tr>
                          <td colSpan={3} className="text-center">
                            <p className="text-muted small text-center m-0">Belum ada Tujuan&nbsp; <Button color="success" size="sm" outline onClick={() => this.addModal.open()}>Tambah tujuan</Button></p>
                          </td>
                        </tr>
                  }
                </tbody>
              </table>
              <CardFooter>
                <Row noGutters>
                  <Col sm="7">
                    <ButtonGroup size="sm">
                      <Button disabled={this.state.totalPage === 0 | 1} onClick={this.prev} color="primary">Prev</Button>
                      <Button color="light" disabled>{this.state.page}/{this.state.totalPage === 0 ? 1 : this.state.totalPage}</Button>
                      <Button disabled={this.state.totalPage === 0 | 1} onClick={this.next} color="primary">Next</Button>
                    </ButtonGroup>
                  </Col>
                  <Col>
                    <InputGroup size="sm">
                      <Input value={this.state.limit} onChange={this.limitChange} type="number"  />
                      <InputGroupAddon addonType="append">Limit/Halaman</InputGroupAddon>
                    </InputGroup>
                  </Col>
                </Row>
              </CardFooter>
            </Card>
          </Col>
          <Col md="6" className="pl-1">
            {/* <button className="btn btn-primary btn-sm rounded-pill mx-2" onClick={() => this.requirementModal.open()}>Tambah Dokumen</button> */}
            <RequirementTable models={this.props.models} />
          </Col>
        </Row>
        <div className="card-body">
          {/* <Card>
            <CardBody>
            </CardBody>
          </Card> */}
        </div>
        <AddPurpose addRequirement={() => this.requirementModal.open()} add={this.addPurpose} models={this.props.models} ref={ref => this.addModal = ref} />
        <Requirement ref={ref => this.requirementModal = ref} models={this.props.models} />
        <Requirements requirements={this.state.requirements} close={() => this.setState({ modal: false })} open={this.state.modal} />
        <EditPurpose models={this.props.models} reload={this.getPurpose} ref={ref => this.editModal = ref} requirements={this.state.requirements} />
      </>
    )
  }

}

const Tr = props => (<tr>
  <td>{props.index}</td>
  <td>{props.purpose.name}</td>
  <td>
    <div className="btn-group btn-group-sm">
      <button className="btn btn-primary" onClick={() => props.requirement(props.purpose)}><FaBookReader /></button>
      <button className="btn btn-warning" onClick={() => props.edit(props.purpose)}><FaRegEdit /></button>
      <button onClick={() => props.onClick(props.purpose)} className="btn btn-danger"><FaTrash /></button>
    </div>
  </td>
</tr>)

const Requirements = props => (
  <Modal toggle={props.close} isOpen={props.open}>
    <ModalHeader toggle={props.close} className="bg-primary text-white">
      {props.requirements.name}
    </ModalHeader>
    <Table bordered hover striped responsive>
      <thead>
        <tr>
          <th>Persyaratan</th>
          <th>Opsional</th>
        </tr>
      </thead>
      <tbody>
        {props.requirements.requirements.length > 0 ? props.requirements.requirements.map((requirement, i) => (
          <tr key={i}>
            <td>{requirement.name}</td>
            <td>{(!requirement.required ? 'Opsional' : 'Penting')}</td>
          </tr>
        )) :
          <tr>
            <td colSpan={2}><p className="m-0 text-muted text-center">Tidak ada persyaratan</p></td>
          </tr>}
      </tbody>
    </Table>
    <ModalFooter>
      <button className="btn btn-outline-dark btn-sm rounded-pill" onClick={props.close}>Tutup</button>
    </ModalFooter>
  </Modal>
)