import React, { Component } from 'react'
import { Card, Spinner, CardFooter, ButtonGroup, Button, Table, Input, InputGroup, InputGroupAddon, Row, Col } from 'reactstrap';

export default class RequirementTable extends Component {
  state = {
    open: false,
    name: "",
    requirements: {
      rows: [],
      count: 1
    },
    page: 1,
    offset: 0,
    limit: 10,
    totalPage: 1,
    submitting: false,
    requirement: {
      name: ''
    },
    edit: false,
    loading: false
  }

  open = () => {
    this.setState({ open: true }, this.getList);
  }

  getList = (loading = false) => {
    const { models: { Requirement } } = this.props;
    const { offset, limit } = this.state;
    if (loading) this.setState({ loading: true });
    Requirement.collection({ limit, offset }).then(requirements => {
      const totalPage = Math.ceil(requirements.count / limit);
      this.setState({ requirements, totalPage, loading: false });
    });
  }

  componentDidMount = () => {
    this.getList(true);
  };


  cancel = () => {
    this.setState({ requirement: { name: '' }, edit: false, name: '' });
  }

  close = () => {
    this.setState({ open: false });
  }

  edit = requirement => this.setState({ edit: true, requirement, name: requirement.name });

  delete = requirement => {
    requirement.delete().then(() => this.setState({ requirement: { name: '' }, edit: false }, () => this.getList()))
  }

  update = () => {
    const { requirement, name } = this.state;
    requirement.update({ name }).then(() => this.setState({ name: '', requirement: { name: '' }, edit: false }, () => this.getList()));
  }

  addRequirement = ev => {
    ev.preventDefault();
    const { models: { Requirement } } = this.props;
    const { name } = this.state;
    this.setState({ submitting: true }, () => {
      Requirement.create({ name }).then(() => {
        this.setState({ name: '', submitting: false }, () => this.getList());
      })
    })
    return false;
  }

  next = () => {
    // const { models: { Schedule } } = this.props;
    let { totalPage, offset, limit, page } = this.state;
    page = page < totalPage ? page + 1 : totalPage;
    offset = parseInt((page - 1) * limit);
    this.setState({ offset, page }, () => this.getList());
  }

  prev = () => {
    let { limit, page, totalPage } = this.state;
    page = (page > 1) ? (page - 1) : 1;
    const offset = parseInt((page - 1) * limit);
    this.setState({ offset, totalPage, page }, () => this.getList());
  }

  limitChange = ev => {
    this.setState({ limit: ev.target.value, offset : 0, page : 1 }, () => this.getList());
  }

  render() {
    return (
      <>
        <form action="" className="px-2" method="post">
          <InputGroup size="sm">
            <Input readOnly={this.state.submitting} placeholder="Nama Dokumen" onChange={ev => this.setState({ name: ev.target.value })} value={this.state.submitting ? 'Menambah Dokumen' : this.state.name} name="name" />
            <InputGroupAddon addonType="append">
              {this.state.edit && <Button color="danger" type="button" outline onClick={this.cancel}>Cancel</Button>}
              {this.state.edit && <Button type="submit" color="success" outline onClick={this.update}>Simpan</Button>}
              {!this.state.edit && <Button type="submit" color="success" outline onClick={this.addRequirement} >Tambah</Button>}
            </InputGroupAddon>
          </InputGroup>
        </form>
        <Card className="border-0 my-2">
          <Table striped hover bordered>
            <thead>
              <tr>
                <th>No.</th>
                <th>Nama</th>
                <th>Edit/Hapus</th>
              </tr>
            </thead>
            <tbody>
              {
                this.state.loading ?
                  <tr>
                    <td colSpan={3}><p className="text-muted m-0 text-center"><Spinner size="sm" color="secondary" /> Loading</p></td>
                  </tr>
                  :
                  this.state.requirements.rows.length > 0 ?
                    this.state.requirements.rows.map((req, i) => (<Tr key={i} edit={this.edit} deleteRequirement={this.delete} req={req} index={(i + 1) + this.state.offset} />)) :
                    <tr>
                      <td colSpan={3}><p className="text-muted m-0 text-center">Belum Ada Dokumen Persyaratan</p></td>
                    </tr>
              }
            </tbody>
          </Table>
          <CardFooter>
            <Row noGutters>
              <Col sm="7">
                <ButtonGroup size="sm">
                  <Button disabled={this.state.totalPage === 0 | 1} onClick={this.prev} color="primary">Prev</Button>
                  <Button disabled color="light">{`${this.state.page}/${this.state.totalPage > 0 ? this.state.totalPage : 1}`}</Button>
                  <Button disabled={this.state.totalPage === 0 | 1} onClick={this.next} color="primary">Next</Button>
                </ButtonGroup>
              </Col>
              <Col>
                <InputGroup>
                  <Input value={this.state.limit} onChange={this.limitChange} type="number" />
                  <InputGroupAddon addonType="append">Limit/Halaman</InputGroupAddon>
                </InputGroup>
              </Col>
            </Row>
          </CardFooter>
        </Card>
      </>
    )
  }
}

const Tr = props => (
  <tr>
    <td>{props.index}</td>
    <td>{props.req.name}</td>
    <td>
      <div className="btn-group btn-group-sm">
        <button onClick={() => props.edit(props.req)} className="btn btn-warning">Edit</button>
        <button onClick={() => props.deleteRequirement(props.req)} className="btn btn-danger">Hapus</button>
      </div>
    </td>
  </tr>
)