import React from 'react'
import {NavLink} from 'react-router-dom'
import {ListGroupItem} from 'reactstrap';

export default props => (<div className="list-group list-group-flush">
  <NavLink to="/dashboard/main" className="list-group-item list-group-item-action">Main Dashboard</NavLink>
  <NavLink to="/dashboard/antrian" className="list-group-item list-group-item-action">Antrian Hari Ini</NavLink>
  <NavLink to="/dashboard/rekap-antrian" className="list-group-item list-group-item-action">Rekap Antrian</NavLink>
  {/* <ListGroupItem disabled>Rekap Antrian</ListGroupItem> */}
  <div className="logout-btn">
    <button onClick={() => {
      props.provider.remove().then(() => {
        props.logout()
      })
    }} className="btn btn-outline-dark btn-lg btn-block rounded-pill">Logout</button>
  </div>
</div>)
