import React, { Component } from 'react';
import DashContent from '../components/DashContent';
import UserProfile from '../components/UserProfile';
import DashLink from '../components/DashLinks';
import OperatorLink from '../components/OperatorLink';
import { Route, Redirect } from 'react-router-dom';

export default class Dashboard extends Component {
  state = {
    height: 0,
    user : {type : '...', name : 'Loading'},
    login : true
  }

  componentDidMount = () => {
    const {login} = this.props;
    const height = document.querySelector('#dash-wrapper').clientHeight;
    this.setState({ height, login });
    // provider.get().then(user => {
    //   this.setState({ user })
    // }).catch(() => this.setState({ login : false }))
  };

  componentWillReceiveProps = props => {
    const { login } = props;
    this.setState({ login });
  }

  logout = () => {
    const { provider, check }  = this.props;
    provider.remove().then(() => {
      this.setState({ login : false })
      check();
    })
  }

  render() {
    if (!this.props.login) return <Redirect to='/login' />
    return (
      <>
        <div className="bg-half" />
        <div className="wrapper p-3">
          <div style={{ height: '100%' }} id="dash-wrapper" className="card border-0 shadow">
            <div style={{ height : this.state.height }} className="row no-gutters">
              <div className="col-md-3 border-right position-relative">
                <UserProfile data={this.props.user} />
                { this.props.user.type === 'administrator' ?
                <DashLink provider={this.props.provider} logout={this.logout} />
                :
                this.props.user.type === 'operator' ?
                <OperatorLink provider={this.props.provider} logout={this.logout} />
                :
                ''
                }
              </div>
              <Route path={`${this.props.match.path}/`} render={(p) => <DashContent socket={this.props.socket} user={this.props.user} provider={this.props.provider} models={this.props.models} {...p} />} />
            </div>
          </div>
        </div>
      </>
    )
  }
}
